












import { requestip } from '../api/api.js'
export default{
	data(){
		return{
			data:[],
			flexblean:false,
			showloadMore:false,
			loadMoreText:"加载中..."
		}
	},
	methods:{
		// 初始化数据
		banpushInit(e){
			uni.request({
				url: requestip()+'/thirdreturn/index/themegoods?theme_id='+e.id,
				method: 'GET',
				data: {},
				success: res => {
					// console.log('banpust数据;',res.data)
					this.data=res.data.theme_list_get_response.goods_list;
					uni.hideLoading();
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onLoad:function(e) {
		uni.hideShareMenu();
		uni.showLoading({
			title:'加载中...'
		})
		// 导航栏的标题
		uni.setNavigationBarTitle({
			title:e.name
		})
		// console.log('bpush',e);
		this.banpushInit(e);
	},
	onReachBottom() {
		this.showloadMore=true;
		this.loadMoreText='没有更多数据了';
	}
}
